import React, { InputHTMLAttributes } from 'react';

interface SearchInputProps extends InputHTMLAttributes<HTMLInputElement> {
  error?: string;
}

const SearchInput: React.FC<SearchInputProps> = ({ error, className = '', ...props }) => {
  return (
    <div className="relative">
      <input
        type="text"
        className={`
          w-full rounded-lg border border-gray-300 bg-white pl-4 pr-10 py-3 text-sm outline-none transition-all 
          focus:border-brand-500 focus:ring-1 focus:ring-brand-500 
          dark:border-gray-800 dark:bg-gray-900 dark:focus:border-brand-500 text-gray-900 dark:text-white
          ${error ? 'border-error-500 focus:border-error-500 focus:ring-error-500' : ''}
          ${className}
        `}
        {...props}
      />
      <svg
        className="absolute right-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400"
        fill="none"
        stroke="currentColor"
        viewBox="0 0 24 24"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth={2}
          d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
        />
      </svg>
      {error && (
        <p className="mt-1 text-sm text-error-500">{error}</p>
      )}
    </div>
  );
};

export default SearchInput;
